
import '../App.css';
import { Card, Avatar } from 'antd';
import { EditOutlined, EllipsisOutlined, RiseOutlined } from '@ant-design/icons';

const { Meta } = Card;


const Countrystats = (props) => {

    return (
        <div className="covidwrappercontainer">

<Card
        style={{ width: 600, marginBottom:30}}
        cover={
          <img
            alt="example"
            src="https://w.ndtvimg.com/sites/3/2020/05/07153830/coronavirus_COVID19_cases_uttarakhand_istock_660x300.jpg"
          />
        }
   
      >
        <Meta
          avatar={<Avatar src="https://png4u.com/wp-content/uploads/2020/02/India-Flag-Transparent-PNG-Circle-Icon.png" />}
          title={props.countryName}
          description="Covid-19 Stats of country Today"
        
        />
     
        <div style={{display:'flex', justifyContent:'space-around', marginTop:'30px'}}>
            <div>
            <RiseOutlined key="setting" /> Confirmed: <strong>{props.confirmed}</strong>
            </div>
            <div>
            <RiseOutlined key="setting" /> Critical: <strong>{props.critical}</strong>
            </div>
            <div>
            <RiseOutlined key="setting" /> Deaths: <strong>{props.death}</strong>
            </div>
        </div>
        


        

      </Card>

        </div>
    )
  

  }


export default Countrystats;