import React, { Component } from 'react';
import './index.css';
import { Layout, Menu } from 'antd';
import Covidwrapper from './components/covid-wrapper'
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  UserOutlined,
  VideoCameraOutlined,
} from '@ant-design/icons';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Countrystatswrapper from './components/countrystats-wrapper';



const { Header, Sider, Content } = Layout;

class App extends Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    this.setState({
      collapsed: true,
    });
  };

  render() {
    return (
      
        <Layout>
          <Router>
          <Sider trigger={null} collapsible collapsed={this.state.collapsed}>
            <div className="logo" />


         
            <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
              <Menu.Item key="1" icon={<UserOutlined />}>
                <Link to="/">COVID19 News</Link>
              </Menu.Item>
              <Menu.Item key="2" icon={<VideoCameraOutlined />}>
                <Link to="/countrystats-wrapper">India Stats</Link>
              </Menu.Item>
           

            </Menu>




          </Sider>


          <Layout className="site-layout">
            <Header className="site-layout-background" >
              {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
                className: 'trigger',
                onClick: this.toggle,
              })}
            </Header>
            <Content
              className="site-layout-background"
              style={{
                margin: '24px 16px',
                padding: 24,
                minHeight: 280,
              }}
            >
            
                            <Route path="/countrystats-wrapper" component={Countrystatswrapper} />
                            <Route exact path="/" component={Covidwrapper} />
                            
       
           
             
            </Content>
          </Layout>
          </Router>
        </Layout>
   
    );
  }
}

export default App;
