import React, { useState, useEffect } from 'react';
import Covidcard from './covidcard';
import '../App.css';
import axios from 'axios';
import { Spin } from 'antd';



const Covidwrapper = () => {
    const [data, setData] = useState({ hits: [] });
 
    useEffect(() => {
      const fetchData = async () => {
        const result = await axios({
            "method": "GET",
            "url": "https://vaccovid-coronavirus-vaccine-and-treatment-tracker.p.rapidapi.com/api/news/get-coronavirus-news/0",
            "headers": {
              "content-type": "application/octet-stream",
              "x-rapidapi-host": "vaccovid-coronavirus-vaccine-and-treatment-tracker.p.rapidapi.com",
              "x-rapidapi-key": "cd4cd593a3mshfb11a480cc3b188p14cdf9jsnd2e2e35e5945"
            }, "params": {
              "language_code": "en"
            }
          });
   
        setData(result.data);
 
       
      };
   
      fetchData();
    }, []);
   
    return (
        <div className="covidwrappercontainer">

        {
            data.news ?
            data && data.news && 
       data.news.map((item) => <Covidcard key={item.news_id} newsTitle={item.title} newsThumb={item.urlToImage}  />)
: <Spin />
        }

        </div>
    )
  

  }


export default Covidwrapper;