import React, { useState, useEffect } from 'react';
import Countrystats from './countrystats';
import '../App.css';
import axios from 'axios';
import { Spin } from 'antd';



const Countrystatswrapper = () => {
  const [data, setData] = useState( [] );

  useEffect(() => {
    const fetchData = async () => {
      const result = await axios({
        "method": "GET",
        "url": "https://covid-19-data.p.rapidapi.com/country?name=india",
        "headers": {
          "content-type": "application/octet-stream",
          'x-rapidapi-key': 'cd4cd593a3mshfb11a480cc3b188p14cdf9jsnd2e2e35e5945',
          'x-rapidapi-host': 'covid-19-data.p.rapidapi.com'
        }, "params": {
          "language_code": "en"
        }
      });

   setData(result.data);
      console.log(result.data);

    };

    

    fetchData();
  }, []);


  return (
    <div className="covidwrappercontainer">

{
    data?
    data && data.map((item, i) => <Countrystats key={i}  countryName={item.country} confirmed={item.confirmed} death={item.deaths} critical={item.critical}/>)
   : <Spin />
   }

    </div>
  )


}


export default Countrystatswrapper;