import React from 'react';
import { Card, Avatar } from 'antd';
import { EditOutlined, EllipsisOutlined, RiseOutlined } from '@ant-design/icons';

const { Meta } = Card;

const Covidcard = (props) =>{
    return(
        <Card
        style={{ width: 300, marginBottom:30}}
        cover={
          <img
            alt="example"
            src={props.newsThumb}
          />
        }
        actions={[
          <RiseOutlined key="setting" />,
          <EditOutlined key="edit" />,
          <EllipsisOutlined key="ellipsis" />,
        ]}
      >
        <Meta
          avatar={<Avatar src={props.newsThumb} />}
          title={props.newsTitle}
          description={props.newsTitle}
        />
      </Card>
    )
}

export default Covidcard;